html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  min-height: 100vh; }

@keyframes moveRight {
  0% {
    transform: translateX(0px); }
  50% {
    transform: translateX(10px); }
  100% {
    transform: translateX(0px); } }

@keyframes moveDown {
  0% {
    transform: translate(20px, 0px) rotate(90deg); }
  50% {
    transform: translate(20px, 10px) rotate(90deg); }
  100% {
    transform: translate(20px, 0px) rotate(90deg); } }

.main-header {
  width: 100%;
  top: 0;
  padding: 16px;
  background: rgba(143, 138, 138, 0.7);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  z-index: 10; }

.navigation-trigger {
  border: none;
  background: none;
  text-decoration: none;
  color: #fff;
  cursor: pointer; }

.navigation {
  position: absolute;
  display: flex;
  transform: translateX(100%);
  top: 0;
  right: 0;
  background: #8f8a8a;
  width: 300px;
  min-height: 100vh;
  transition: transform 0.5s; }
  .navigation__show {
    transform: translateX(0%); }
  .navigation__list {
    margin: 55px 0 0 15px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    list-style: none; }
  .navigation__item {
    margin-bottom: 30px; }
  .navigation__link {
    text-decoration: none;
    color: #fff; }
    .navigation__link:hover {
      color: #ccc5c5; }
  .navigation-hide {
    border: none;
    background: none;
    color: #fff;
    position: fixed;
    top: 20px;
    right: 25px; }
    .navigation-hide::before {
      content: "";
      display: block;
      position: absolute;
      top: -10px;
      left: 10px;
      width: 3px;
      height: 20px;
      background: #fff;
      transform: rotate(-45deg); }
    .navigation-hide::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 3px;
      background: #fff;
      transform: rotate(-45deg); }

.person {
  width: 100%;
  height: 600px;
  background: url("../assets/images/fog.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #8f8a8a; }
  .person__title {
    padding-bottom: 50px; }
  .person__citation {
    padding-bottom: 90px; }
  .person__button {
    text-decoration: none;
    padding: 12px 35px;
    background: none;
    color: #fff;
    border-bottom: 2px solid #fff;
    border-right: 2px solid #fff;
    position: relative;
    transition: background 0.5s; }
    .person__button:hover {
      background: rgba(143, 138, 138, 0.4); }
    .person__button::before {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 0%;
      height: 2px;
      background: #fff;
      transition: width 0.6s; }
    .person__button:hover::before {
      width: 100%; }
    .person__button::after {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 2px;
      height: 0%;
      background: #fff;
      transition: height 0.6s; }
    .person__button:hover::after {
      height: 100%; }

.about {
  position: relative;
  height: 600px;
  color: #8f8a8a; }
  .about__title {
    position: absolute;
    top: 10%;
    font-size: 30px;
    left: 50%;
    transform: translate(-50%, -50%); }
  .about__box {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center; }
  .about__who,
  .about .who {
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50vw;
    background: none;
    padding-bottom: 120px; }
    .about__who__title,
    .about .who__title {
      max-width: 120px;
      line-height: 50px;
      text-align: center;
      font-size: 30px; }
    .about__who__drawn-arrow,
    .about .who__drawn-arrow {
      width: 70px;
      height: 40px;
      background: none;
      color: #fff;
      border-bottom: 3px solid #857e7e;
      animation: moveRight 1s infinite; }
      .about__who__drawn-arrow::before,
      .about .who__drawn-arrow::before {
        content: "";
        display: block;
        width: 35px;
        height: 3px;
        margin-left: 40px;
        margin-top: 25px;
        background: #857e7e;
        transform: rotate(45deg); }
      .about__who__drawn-arrow::after,
      .about .who__drawn-arrow::after {
        content: "";
        display: block;
        width: 35px;
        height: 3px;
        margin-left: 40px;
        margin-top: 20px;
        background: #857e7e;
        transform: rotate(-45deg); }
  .about__info,
  .about .info {
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50vw;
    background: none; }
    .about__info__image,
    .about .info__image {
      width: 200px;
      height: 200px;
      border-radius: 50%; }
    .about__info__paragraph,
    .about .info__paragraph {
      color: #5c5555;
      margin-top: 20px;
      max-width: 400px;
      line-height: 25px; }
    .about__info__link,
    .about .info__link {
      padding-top: 20px;
      text-decoration: none;
      color: #494545;
      font-weight: bold; }
      .about__info__link:hover,
      .about .info__link:hover {
        color: black; }

.trivia__title {
  text-align: center;
  padding: 40px 0 60px 0; }

.trivia .grid {
  padding: 32px 16px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-column-gap: 90px;
  grid-row-gap: 40px; }

.trivia .section {
  display: grid;
  grid-template-rows: 50px 1fr;
  border: 2px solid rgba(143, 138, 138, 0.7);
  border-radius: 4px;
  transition: box-shadow 0.4s; }
  .trivia .section:hover {
    box-shadow: 2px 2px 12px 0px; }
  .trivia .section__title {
    font-size: 17px;
    background: #fff;
    padding: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0; }
  .trivia .section__content {
    background: url("../assets/images/programming.jpg");
    padding: 8px 12px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; }
  .trivia .section__paragraph {
    line-height: 20px; }
  .trivia .section__link {
    text-decoration: none;
    color: #8f8a8a;
    padding: 10px 15px; }

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .contact__title {
    font-size: 30px;
    text-align: center;
    width: 100%;
    color: #fff;
    height: 200px;
    background: url("../assets/images/fog.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover; }

.footer {
  width: 100%;
  height: 300px;
  background: rgba(143, 138, 138, 0.8);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .footer__box {
    display: flex;
    justify-content: center;
    align-items: center; }
  .footer__list {
    display: flex;
    list-style: none;
    padding: 0; }
  .footer__item {
    margin-left: 20px;
    transition: all 0.4s; }
    .footer__item:hover {
      transform: scale(1.2); }
  .footer__image {
    width: 30px;
    height: 30px; }
  .footer__link--top {
    text-decoration: none;
    display: none;
    position: fixed;
    background: none;
    padding: 25px 5px;
    bottom: 40px;
    right: 20px;
    margin-right: 10px;
    border-right: 2px solid #857e7e; }
    .footer__link--top-visible {
      display: block; }
    .footer__link--top::before {
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      height: 60%;
      top: 0;
      left: 2px;
      background: #857e7e;
      transform: rotate(30deg); }
    .footer__link--top::after {
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      height: 60%;
      top: 0px;
      left: 18px;
      background: #857e7e;
      transform: rotate(-30deg); }
  .footer__info {
    border-top: 2px solid rgba(0, 0, 0, 0.548);
    padding-top: 35px; }

@media screen and (max-width: 700px) {
  .about__who__drawn-arrow, .about .who__drawn-arrow {
    animation: moveDown 1s infinite; } }
